import React, { useContext } from 'react';
import { Typography, Box, Container } from '@mui/material';

import s from './UserInfo.module.css';
import { UserContext } from '../../context/UserContext';

const UserInfo = () => {
  const { currentUser } = useContext(UserContext);

  return (
    <Container>
      <section className={s.userInfo}>
        <Typography
          sx={{
            fontFamily: "Pangolin",
          }}
          variant="h3"
          className={s.title}
        >
           
        </Typography>
        <Box className={s.info}>
          <div className={s.box}>
            <Typography className={s.infoTitle} variant="h5">
              
            </Typography>
          </div>
          <div className={s.box}>
            <Typography lineHeight="2" className={s.name}>
              <b>:</b> {currentUser.name}
            </Typography>
            <Typography lineHeight="2" className={s.about}>
              <b>:</b> {currentUser.about}
            </Typography>
            <Typography lineHeight="2" className={s.group}>
              <b>:</b> {currentUser.group}
            </Typography>
            <Typography lineHeight="2" className={s.contacts}>
              <b>:</b> {currentUser.email}
            </Typography>
          </div>
        </Box>
      </section>
    </Container>
  );
};

export default UserInfo;
